import mixAdvert from '@/components/mix-advert/vue/mix-advert';
import json from '@/json'
import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
import mixLoadMore from '@/components/mix-load-more/mix-load-more';
import request from '@/common/requesst/interface.js'
import {mapMutations} from 'vuex'
let windowWidth = 0, scrollTimer = false, tabBar;
export default {
	components: {
		mixPulldownRefresh,
		mixLoadMore,
		mixAdvert
	},
	data() {
		return {
			tabCurrentIndex: 0, //当前选项卡索引
			scrollLeft: 0, //顶部选项卡左滑距离
			enableScroll: true,
			tabBars: [],
		}
	},
	computed: {
		advertNavUrl(){
			let data = {
				title: '测试跳转新闻详情',
				author: '测试作者',
				time: '2019-04-26 21:21'
			}
			return `/pages/details/details?data=${JSON.stringify(data)}`;
		} 
	},
	async onLoad() {
		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		this.loadTabbars();
	},
	onReady(){
		/**
		 * 启动页广告 使用文档（滑稽）
		 * 1. 引入组件并注册 
		 * 		import mixAdvert from '@/components/mix-advert/vue/mix-advert';
		 *      components: {mixAdvert},
				 <!-- #ifndef MP -->
					<mix-advert 
						ref="mixAdvert" 
						:timedown="8" 
						imageUrl="/static/advert.jpg"
						:url="advertNavUrl"
					></mix-advert>
				<!-- #endif -->
		 * 	2. 调用组件的initAdvert()方法进行初始化
		 * 
		 *  初始化的时机应该是在splash关闭时，否则会造成在app端广告显示了数秒后首屏才渲染出来
		 */
		//


	},
	methods: {
		...mapMutations(['setCategory']),
		/**
		 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
		 * 这里直接写的
		 * mixin使用方法看index.nuve
		 */
		//获取分类
		loadTabbars(){
			request({
				url: '/admin/category',
				method: 'GET'
			}).then( res => {
				if ( res.data.code === 1000) {
					let tabList = res.data.data
					tabList.forEach(item=>{
						item.newsList = [];
						item.list = []
						item.loadMoreStatus = 0;  //加载更多 0加载前，1加载中，2没有更多了
						item.refreshing = 0;
						item.page = {
							startPage: 1,
							pageSize: 10,
							totalPage: null
						}
					})
					
					this.tabBars = tabList;
					this.loadNewsList('add');
					this.setCategory(res.data.data)
				}
			})
		},
		//新闻列表
		loadNewsList(type){
			let tabItem = this.tabBars[this.tabCurrentIndex];
			
			//type add 加载更多 refresh下拉刷新
			if(type === 'add'){
				if(tabItem.loadMoreStatus === 2){
					return;
				}
				tabItem.loadMoreStatus = 1;
			}





			
			//setTimeout模拟异步请求数据

				if(type === 'refresh'){
					tabItem.list = []; //刷新前清空数组
					tabItem.page.startPage = 1
				}
				request({
					url: '/posts/getByCategory/' + tabItem.categoryId,
					method: 'POST',
					data: tabItem.page
				}).then( res => {
					this.tabBars[this.tabCurrentIndex].page.startPage = res.data.data.startPage
					this.tabBars[this.tabCurrentIndex].page.pageSize = res.data.data.pageSize
					this.tabBars[this.tabCurrentIndex].page.totalPage = res.data.data.totalPage
					res.data.data.list.forEach( item => {
						this.tabBars[this.tabCurrentIndex].list.push(item)
					})
					if (res.data.data.list.length === 0 || res.data.data.startPage === res.data.data.totalPage) {
						this.tabBars[this.tabCurrentIndex].loadMoreStatus = 2
					}
				}).catch( res => {
					console.log(res)
				})
				//下拉刷新 关闭刷新动画
				if(type === 'refresh'){
					this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();



					tabItem.loadMoreStatus = 0;
				}
				//上滑加载 处理状态
				if(type === 'add'){
					tabItem.loadMoreStatus = tabItem.page.startPage < tabItem.page.totalPage ? 2: 0;
				}
			
			
		},
		//新闻详情
		navToDetails(item){
			let data = item.postId
			console.log(data)
			uni.navigateTo({
				url:'../details/details?data='+ data
			})
		},
		
		//下拉刷新
		onPulldownReresh(){
			this.loadNewsList('refresh');
		},
		//上滑加载
		loadMore(){
			this.loadNewsList('add');
		},
		//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
		setEnableScroll(enable){
			if(this.enableScroll !== enable){
				this.enableScroll = enable;
			}
		},

		//tab切换
		async changeTab(e){
			
			if(scrollTimer){
				//多次切换只执行最后一次
				clearTimeout(scrollTimer);
				scrollTimer = false;
			}
			let index = e;
			//e=number为点击切换，e=object为swiper滑动切换
			if(typeof e === 'object'){
				index = e.detail.current
			}
			if(typeof tabBar !== 'object'){
				tabBar = await this.getElSize("nav-bar")
			}
			//计算宽度相关
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0; 
			let nowWidth = 0;
			//获取可滑动总宽度
			for (let i = 0; i <= index; i++) {
				let result = await this.getElSize('tab' + i);
				width += result.width;
				if(i === index){
					nowWidth = result.width;
				}
			}
			if(typeof e === 'number'){
				//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
				this.tabCurrentIndex = index; 
			}
			//延迟300ms,等待swiper动画结束再修改tabbar
			scrollTimer = setTimeout(()=>{
				if (width - nowWidth/2 > windowWidth / 2) {
					//如果当前项越过中心点，将其放在屏幕中心
					this.scrollLeft = width - nowWidth/2 - windowWidth / 2;
				}else{
					this.scrollLeft = 0;
				}
				if(typeof e === 'object'){
					this.tabCurrentIndex = index; 
				}
				this.tabCurrentIndex = index; 
				
				
				//第一次切换tab，动画结束后需要加载数据
				let tabItem = this.tabBars[this.tabCurrentIndex];
				if(this.tabCurrentIndex !== 0 && tabItem.loaded !== true){
					this.loadNewsList('add');
					tabItem.loaded = true;
				}
			}, 300)
			
		},
		//获得元素的size
		getElSize(id) { 
			return new Promise((res, rej) => {
				let el = uni.createSelectorQuery().select('#' + id);
				el.fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
	}
}
